@media all and (max-width: $mobile) {
  .main-nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 120px;
    z-index: 2;
  }

  .main-nav::before {
    background-color: $black-color;
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 2;
  }
}

.main-nav__container {
  margin: 0 auto;
  max-width: 1296px;
  padding: 30px 30px;
  display: grid;
  grid-template-columns: min-content auto min-content;
  align-items: center;
  grid-column-gap: 50px;

  @media all and (max-width: $mobile) {
    grid-template-columns: min-content auto;
  }
}

.main-nav__link-logo {
  height: 60px;
  width: 60px;
  position: relative;
  z-index: 2;
}

.main-nav--border-bottom {
  border-bottom: 1px solid $white-color-opacity;
  padding: 30px 0;
}

@media all and (max-width: $mobile) {
  .main-nav__btn {
    display: none;
  }
}

.main-nav__burger-menu {
  margin-left: auto;
}

@media all and (max-width: $mobile) {
  .main-nav__site-list {
    position: fixed;
    top: 120px;
    height: 100%;
    width: 100%;
    text-align: left;
    background: #306a6a;
    z-index: 1;
    flex-direction: column;
    align-items: center;
    transition: top 1s ease;
    max-height: calc(100vh - 120px);
    overflow: auto;
  }

  .main-nav__container--nojs .main-nav__site-list {
    position: static;
  }

  .main-nav__container--nojs {
    grid-template-columns: min-content auto min-content;
    grid-template-rows: auto auto;
    padding: 0;
    grid-column-gap: 0;
  }

  .main-nav__container--nojs .main-nav__burger-menu {
    display: none;
  }

  .main-nav__container--nojs .main-nav__site-list {
    grid-row: 2 / 3;
    grid-column: 1 / -1;
  }

  .main-nav__container--nojs .main-nav__link-logo {
    padding: 30px;
    grid-column: 1 / 2;
  }

  .main-nav__container--closed .main-nav__site-list {
    top: -100%;
  }
}


